.center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .mobile {
    width: 276px;
    height: 580px;
    background: #fff;
    border-radius: 30px;
    border: 2px solid rgba(0, 0, 0, 0.3);
    box-sizing: border-box;
    box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.3);
  }
  
  .screen {
    position: relative;
    background: #fff;
    width: 256px;
    height: 455px;
    margin: 58px auto;
    overflow: hidden;
    border: 1px solid #262626;
  }
  
  .weather-display {
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  /* .screen.active {
    background: url()
  } */
  
  .home {
    position: absolute;
    bottom: 15px;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 40px;
    background: #fff;
    box-shadow: inset 0 0 7px rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    border: 2px solid #cecece;
    box-sizing: border-box;
  }
  
  .inner {
    position: absolute;
    top: 32px;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 4px;
    background: #262626;
    border-radius: 2px;
    box-sizing: border-box;
  }
  
  .inner:before {
    content: "";
    position: absolute;
    top: -2px;
    left: -22px;
    background: #262626;
    width: 8px;
    height: 8px;
    border-radius: 50%;
  }
  
  .inner:after {
    content: "";
    position: absolute;
    top: -16px;
    left: 50%;
    background: #262626;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    transform: translateX(-50%);
  }
  
  .volume {
    margin: 0;
    padding: 0;
    position: absolute;
    top: 90px;
    left: -2px;
  }
  
  .volume li {
    list-style: none;
    width: 2px;
    height: 30px;
    background: #ccc;
    margin: 10px 0;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
  }
  
  .silent {
    margin: 0;
    padding: 0;
    position: absolute;
    top: 50px;
    left: -2px;
  }
  
  .silent li {
    list-style: none;
    width: 2px;
    height: 20px;
    background: #ccc;
    margin: 10px 0;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
  }
  
  .sleep {
    margin: 0;
    padding: 0;
    position: absolute;
    top: 90px;
    right: -2px;
  }
  
  .sleep li {
    list-style: none;
    width: 2px;
    height: 30px;
    background: #ccc;
    margin: 10px 0;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  